/* Chatbot.css */

.chatbot-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: Arial, sans-serif;
    position: relative; /* Needed for absolute positioning */
  }
  
  .response-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .response-text {
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #f8f8f8;
    width: 80%;
    text-align: center;
  }
  
  .button {
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .screen-button {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 20px;
    font-size: 18px;
    background-color: #f8f8f8;
    border: 2px solid #333;
    border-radius: 0; /* Remove border radius */
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s, color 0.2s, border-color 0.2s;
  }
  
  .screen-button:hover {
    background-color: #333;
    color: white;
    border-color: #333;
  }
  
  .screen-button.listening {
    background-color: #e74c3c;
    border-color: #e74c3c;
    color: white;
  }
  